* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

body {
  background-color: gray;
  margin: 0;
  font-size: 12px;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.img {
  width: 40px;
  height: 40px;
  display: block;
}

.navigation {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navbar {
  text-align: center;
  color: #000;
  text-transform: uppercase;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.navigation li:not(:last-child) {
  margin-right: 20px;
}

.navbar:hover, .navbar.is-active {
  color: #00acdc;
}

.navbar.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
